      <vue-dropzone 
      ref="myVueDropzone" 
      id="dropzone" 
      :options="dropzoneOptions"
      @vdropzone-file-added="getExerciseAndAssess"
      @vdropzone-sending="addExerciseToForm"
      @vdropzone-success="sendAssessToServer"
