<script>
import AsysConnectionSnackbar from "@/components/asys-connection-snackbar";
import SvgToolbarAr from "@/components/svg-toolbar-ar";
import ListTileAr from "@/components/list-tile-ar";
import SolutionElementList from "@/components/solution-element-list";
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";

export default {
  name: "Play",
  components: {
    AsysConnectionSnackbar,
    SvgToolbarAr,
    ListTileAr,
    SolutionElementList,
    vueDropzone: vue2Dropzone
  },
  data() {
    return {
      exercise: this.$route.query.exercise,
      exerciseFile: null,
      dropzoneOptions: {
        url: `http://localhost:1955/ASys/assess/${
          this.$route.query.exercise.id
        }/unassessed`,
        autoProcessQueue: false,
        paramName: 'solution',
        method: 'put',
        thumbnailWidth: 150,
        maxFilesize: 50,
        maxFiles: 1,
        addRemoveLinks: true,
        acceptedFiles:
          "application/zip, application/x-zip-compressed, multipart/x-zip",
      }
    };
  },
  methods: {
    downloadResources(exerciseId) {
      this.$service.exercise.getExerciseResources(exerciseId).then(response => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", this.exercise.resourcesURL); //or any other extension
        document.body.appendChild(link);
        link.click();
      });
    },
    downloadInstructions(exerciseId) {
      this.$service.exercise
        .getExerciseInstructions(exerciseId)
        .then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", this.exercise.instructionsURL); //or any other extension
          document.body.appendChild(link);
          link.click();
        });
    },
    getExerciseAndAssess(file) {
      this.$service.exercise.getExerciseTemplate(this.exercise.id)
      .then(httpResponse => {
        this.exerciseFile = new File([httpResponse.data], "exercise.zip", { type: 'application/zip' });
        this.$refs.myVueDropzone.processQueue();
      })
      .catch(err => {
        console.log(err);
      })
    },
    addExerciseToForm(file, xhr, formData) {
      if (this.exerciseFile != null)
        formData.append("exercise", this.exerciseFile);
    },
    sendAssessToServer(file, response) {
      var toUploadSolution = {
        'file': file,
        'mark': 0.0
      }
      const mark = this.$service.exercise.calculateMarkFromAssess(response);
      toUploadSolution.mark = mark;
      this.$service.exercise.uploadAssessedSolution(this.exercise.id, toUploadSolution)
      .then(response => {
        this.$store.commit('exercise/ADD_SOLUTION', response.data);
        setTimeout(() => {
          this.$refs.myVueDropzone.removeAllFiles();
        }, 500)
      })
      .catch(err => {
        console.log(err);
      })
    },
  },
  created: function() {
    this.$store.dispatch('exercise/GET_EXERCISE_SOLUTIONS', this.$route.query.exercise.id)
  }
};
</script>
