<template>
  <div class="asys-connection-snackbar">
    <md-snackbar
      :md-position="position"
      :md-duration="isInfinity ? Infinity : duration"
      :md-active.sync="showSnackbar"
      md-persistent
    >
      <span>{{ text }}</span>
      <md-button
        v-show="state=='NotFound'"
        class="md-primary"
        @click="downloadAsysCLient"
      >{{ $t("components.asysConnectionSnackbar.download") }}</md-button>
    </md-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "asys-connection-snackbar",
  data() {
    return {
      text: this.$t("components.asysConnectionSnackbar.checking"),
      state: "Loading",
      showSnackbar: false,
      position: "center",
      duration: 2000,
      isInfinity: true,
      event: null
    };
  },
  mounted() {
    this.event = setInterval(() => {
      this.pingAsys();
    }, 5000);
  },
  destroyed() {
    clearInterval(this.event);
  },
  methods: {
    pingAsys() {
      axios({
        url: "http://localhost:1955/ASys/ping",
        method: "GET"
      })
        .then(() => {
          this.text = this.$t("components.asysConnectionSnackbar.found");
          this.state = "Success";
          this.showSnackbar = false;
        })
        .catch(() => {
          this.showSnackbar = true;
          this.state = "NotFound";
          this.text = this.$t("components.asysConnectionSnackbar.notFound");
        });
    },
    downloadAsysCLient() {
      this.$service.assess.getAsysClient().then(response => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "AsysClient.zip"); //or any other extension
        document.body.appendChild(link);
        link.click();
      });
    }
  }
};
</script>
